___CSS_LOADER_EXPORT___.push([module.id, ".el-table .warning-row{background:oldlace}.el-table .success-row{background:#f0f9eb}", "",{"version":3,"sources":["webpack://./resources/admin/js/views/rewrites/index.vue"],"names":[],"mappings":"AAkGA,uBACA,kBACA,CAEA,uBACA,kBACA","sourcesContent":["<template>\n    <section>\n        <data-tables :data=\"rewrites\" :table-props=\"tableProps\">\n            <el-table-column\n                type=\"index\"\n                label=\"№\"\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"Имя\"\n                prop=\"name\"\n                sortable\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"e-mail\"\n                prop=\"email\"\n                sortable\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"Статус\"\n                prop=\"status_title\"\n                sortable\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"Дата\"\n                prop=\"date\"\n                sortable\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"Действия\"\n            >\n\n                <template slot-scope=\"scope\">\n                    <el-button\n                        size=\"mini\"\n                        type=\"primary\"\n                        @click=\"handleShow(scope.row.id)\">Подробнее</el-button>\n\n                </template>\n            </el-table-column>\n        </data-tables>\n        <el-dialog\n            title=\"Заявка на рерайт\"\n            :visible.sync=\"dialogVisible\"\n            width=\"90%\"\n            :before-close=\"handleClose\">\n            <show v-if=\"dialogVisible\" :id=\"currentId\"></show>\n        </el-dialog>\n    </section>\n</template>\n<script>\nimport Show from \"./components/show\";\nexport default {\n    components: {Show},\n    data() {\n        return {\n            CanAccess: 'Просмотр \"Заявки на рерайт\"',\n            rewrites: [],\n            dialogVisible:false,\n            currentId:null,\n            type:null,\n            tableProps: {\n                \"row-class-name\": function (row) {\n\n                    if (row.row.status === 0) {\n                        return 'warning-row';\n                    }\n                    return ''\n                }\n            },\n        }\n    },\n    methods: {\n        handleClose() {\n            this.dialogVisible = false;\n            this.currentId = false;\n        },\n        handleShow(id) {\n            this.currentId = id;\n            this.dialogVisible = true;\n        },\n        getRewrites() {\n            axios.get('/api/admin/rewrites')\n                .then((response) => {\n                    this.rewrites = response.data;\n                })\n        },\n    },\n    mounted() {\n        this.getRewrites();\n    }\n}\n</script>\n<style>\n.el-table .warning-row {\n    background: oldlace;\n}\n\n.el-table .success-row {\n    background: #f0f9eb;\n}\n</style>\n"],"sourceRoot":""}]);
